
import MailIcon from "../components/icons/Socials/MailIcon";
import InstaIcon from "../components/icons/Socials/InstagramIcon";
import LinkedInIcon from "../components/icons/Socials/LinkedInIcon";
import TwitterIcon from '../components/icons/Socials/TwitterIcon';
import YoutubeIcon from '../components/icons/Socials/YoutubeIcon';
import seroujIcon from '../assets/images/seroujkh2.jpeg';
const Connect = props => {
    return (
        <div className="col-lg-9 col-xl-7 fh-100 ">
            <div className="white_box"></div>
            <div className="box_container w-100  pos-z " >
                <h1 className="pb-5 fw-900">
                    <span className="border-bottom orange">CONNECT</span>
                </h1>

                <div className="row justify-content-center">

                    <div className="col-lg-5">
                        <div className="round_image_circle ">
                            <img src={seroujIcon} className='padding_img grayscale' alt="Serouj Khandjihanian" />
                        </div>
                    </div>

                    <div className="col-lg-12  mt-4 justify-content-center">
                        <p className="text-center">Do you have a project or an idea in your mind ?  </p>
                        <p className="text-center">Reach out to me and we'll discuss it ! </p>
                        <div className="d-flex align-items-center mt-3 justify-content-center">
                            <MailIcon link="mailto:seroujkh.dev@gmail.com" />
                            <a href="mailto:seroujkh.dev@gmail.com" className="link_orange ml-3">seroujkh.dev@gmail.com</a>
                        </div>
                    </div>
                    <div className="col-lg-6 overflow-hiddden">
                        <div className="d-flex align-items-center mt-3 justify-content-center social_icons_row position-relative">
                            <InstaIcon link="https://www.instagram.com/seroujkh/?hl=en" />
                            <LinkedInIcon link="https://www.linkedin.com/in/serouj-khandjihanian-895997183/" />
                            <TwitterIcon link="https://twitter.com/seroujkh?lang=en" />
                            <YoutubeIcon link="https://www.youtube.com/channel/UC5_CApLUGV_SObJERJkBi7w" />
                        </div>
                    </div>
                </div>
            </div>
        </div>

    )
}

export default Connect;