import React, { useEffect, useState } from 'react';
import { useGLTF } from '@react-three/drei';
import { useFrame } from '@react-three/fiber';

const GLTFModal = ({ path, position, rotation, rotationSpeed, translate, planetType, scale, animatePosition, goLeft, animatePlanet, sat, ...rest }) => {
    const gltf = useGLTF(path, true);
    const mesh = React.useRef();


    const [animatePlanets, setAnimatePlanet] = useState(animatePlanet);
    const [hidePlanet, setHidePlanet] = useState(goLeft);

    useFrame(() =>
        rotationSpeed
            ? ((mesh.current.rotation.x += rotationSpeed[0]),
                (mesh.current.rotation.y += rotationSpeed[1]),
                (mesh.current.rotation.z += rotationSpeed[2]))
            : null,
    );

    useEffect(() => {
        if (animatePlanet == true)
            setAnimatePlanet(true);
        else setAnimatePlanet(false);
        setHidePlanet(goLeft);
    }, [animatePlanet, goLeft]);



    useFrame(() => {
        if (!position) return null;
        let v1 = 100;
        let v2 = 0.1;
        if (window.screen.width < 500) {
            return null;
        }

        // hide rest of the planets

        if (hidePlanet && window.screen.width > 500) {
            if (mesh.current.scale.x > 0) {
                mesh.current.scale.x -= scale[0] / 12;
                mesh.current.scale.y -= scale[0] / 12;
                mesh.current.scale.z -= scale[0] / 12;
            }
            if (mesh.current.position.x > -20) mesh.current.position.x -= v2
            if (mesh.current.position.z > -15) mesh.current.position.z -= v2
        }

        else if (window.screen.width > 500) {
            if (mesh.current.scale.x < scale[0] && planetType !== "connect") {
                mesh.current.scale.x += scale[0] / 12;
                mesh.current.scale.y += scale[0] / 12;
                mesh.current.scale.z += scale[0] / 12;
            } else if (mesh.current.scale.x < scale[0] && planetType === "connect") {
                mesh.current.scale.x += 0.1;
                mesh.current.scale.y += 0.1;
                mesh.current.scale.z += 0.1;
            }
            if (mesh.current.position.x < position[0]) mesh.current.position.x += v2
            if (mesh.current.position.z < position[2]) mesh.current.position.z += v2
        }

        if (animatePlanets) {

            if (mesh.current.position.y > animatePosition[1] && planetType !== "services") {
                mesh.current.position.y -= 10;
            }
            switch (planetType) {
                case "about":
                    if (mesh.current.scale.x < 0.2) {
                        mesh.current.scale.x += 0.1;
                        mesh.current.scale.y += 0.1;
                        mesh.current.scale.z += 0.1;
                    }
                    break;
                case "connect":
                    if (mesh.current.scale.x < 0.4) {
                        mesh.current.scale.x += 0.1;
                        mesh.current.scale.y += 0.1;
                        mesh.current.scale.z += 0.1;
                    }
                    break;
                case "services":
                    if (mesh.current.scale.x < 7) {
                        mesh.current.scale.x += 1;
                        mesh.current.scale.y += 1;
                        mesh.current.scale.z += 1;
                    }
                    // if (mesh.current.position.y < 100) mesh.current.position.y += 10;
                    break;
                case "portfolio":
                    if (mesh.current.scale.x < 0.1) {
                        mesh.current.scale.x += 0.01;
                        mesh.current.scale.y += 0.01;
                        mesh.current.scale.z += 0.01;
                    }
                    break;
                default:
                    break;
            }
        } else {
            if (mesh.current.position.y < position[1]) {
                mesh.current.position.y += 10;
            }
            switch (planetType) {
                case "about":
                    if (mesh.current.scale.x > 0.1) {
                        mesh.current.scale.x -= 0.1;
                        mesh.current.scale.y -= 0.1;
                        mesh.current.scale.z -= 0.1;
                    }
                    break;
                case "connect":
                    if (mesh.current.scale.x > 0.33) {
                        mesh.current.scale.x -= 0.1;
                        mesh.current.scale.y -= 0.1;
                        mesh.current.scale.z -= 0.1;
                    }
                    break;
                case "services":
                    if (mesh.current.scale.x > 3) {
                        mesh.current.scale.x -= 1;
                        mesh.current.scale.y -= 1;
                        mesh.current.scale.z -= 1;
                    }
                    if (mesh.current.position.y > 0) mesh.current.position.y -= 10;
                    break;
                case "portfolio":
                    if (mesh.current.scale.x > 0.05) {
                        mesh.current.scale.x -= 0.01;
                        mesh.current.scale.y -= 0.01;
                        mesh.current.scale.z -= 0.01;
                    }
                    break;
                default: break;
            }
        }

        if (animatePlanets) {
            if (mesh.current.position.x < animatePosition[0]) mesh.current.position.x += 5;
        } else {
            if (mesh.current.position.x > position[0]) mesh.current.position.x -= 5;
        }
    });

    return (
        <mesh ref={mesh} castShadow {...rest} scale={scale} position={position} rotation={rotation} >
            <primitive object={gltf.scene} dispose={null} />
        </mesh>
    );
};

export default GLTFModal;