
import React, { useState, useEffect } from 'react';

import MailIcon from "./icons/Socials/MailIcon";
import InstaIcon from "./icons/Socials/InstagramIcon";
import LinkedInIcon from "./icons/Socials/LinkedInIcon";
import TwitterIcon from './icons/Socials/TwitterIcon';
import YoutubeIcon from './icons/Socials/YoutubeIcon';

const Menu = props => {


    const [menuOpen, setMenuOpen] = useState(props.menuOpen);
    const [activeSidebar, setActiveSidebar] = useState(props.activeSidebar);
    const onBurgerClick = () => {
        props.setMenuOpen(!props.menuOpen);
    }

    useEffect(() => {
        setMenuOpen(props.menuOpen);
        setActiveSidebar(props.activeSidebar);
    }, [props.menuOpen, props.activeSidebar])


    const onMenuItemClick = (name) => {
        props.setActiveSidebar(name);
        // props.setMenuOpen(!menuOpen);
    }

    return (
        <div className='main_menu'>
            <div className={'burger_menu ' + (menuOpen ? ' open ' : '')} onClick={onBurgerClick}>
                <div></div>
                <div></div>
                <div></div>
            </div>
            <div className={"menu_items   " + (menuOpen ? ' open ' : '')} >
                <div className={"item py-4 " + (activeSidebar === "about" ? " active " : "")} onClick={() => onMenuItemClick("about")}> About </div>
                <div className={"item py-4 " + (activeSidebar === "connect" ? " active " : "")} onClick={() => onMenuItemClick("connect")}> Connect </div>
                <div className={"item py-4 " + (activeSidebar === "services" ? " active " : "")} onClick={() => onMenuItemClick("services")}> Skills </div>
                <div className={"item py-4 " + (activeSidebar === "portfolio" ? " active " : "")} onClick={() => onMenuItemClick("portfolio")}> Portfolio </div>
            </div>
            <div className="social_menu_right">
                <InstaIcon link="https://www.instagram.com/seroujkh/?hl=en" />
                <LinkedInIcon link="https://www.linkedin.com/in/serouj-khandjihanian-895997183/" />
                <MailIcon link="mailto:seroujkh.dev@gmail.com" />
            </div>
        </div>
    )
}

export default Menu;