import React, { useState, useEffect } from 'react';
import { data } from './data';


const MainContext = React.createContext({
    portfolio: [],
    activePorfolio: null,
    setActivePortfolio: (item) => { },
});


export const MainContextProvider = (props) => {
    const [portfolio, setPortfolio] = useState(data);
    const [activePorfolio, setActivePortfolio] = useState(null);
    const [showPopup, setShowPopup] = useState(null);


    const setActivePortfolioItem = (item) => {
        setActivePortfolio(item);
    }

    const setShowPopupHandler = (state) => {
        setShowPopup(state);
    }

    return (
        <MainContext.Provider
            value={{
                portfolio: portfolio,
                activePorfolio: activePorfolio,
                setActivePortfolio: setActivePortfolioItem
            }}>
            {props.children}
        </MainContext.Provider>
    );
};

export default MainContext;