
import CloseBtn from "../components/icons/CloseBtn";
import MainContext from "../store/main-context";
import { useState, useEffect, useContext } from "react";

const SinglePortfolio = props => {
    const [showPopup, setShowPopup] = useState(props.showPopup);
    const ctx = useContext(MainContext);
    useEffect(() => {
        setShowPopup(props.showPopup);
    }, [props.showPopup]);

    const [project, setProject] = useState(null);

    useEffect(() => {
        if (ctx.activePorfolio !== "") {
            setProject(ctx.portfolio[ctx.activePorfolio]);
        }
        console.log(project)
    }, [ctx.activePorfolio]);


    return (

        <div className={"portfolio-popup fh-100  " + (showPopup ? ' show ' : '')}>
            <div className="box_container w-100  pos-z " >
                <div className="row align-items-center">

                    <div className="col-lg-3">
                        <div className="square_ratio border-radius-25">
                            <img src={"/dataImages/" + project?.images[0]} className="padding_img" alt={project?.titl} />
                        </div>
                    </div>

                    <div className="col-lg-8 mt-lg-0 mt-4">
                        <h1 className="border-bottom blue text-white pb-3 fw-800">
                            {project?.title}
                        </h1>
                        <p className="fw-700 text-white mt-3 fs-18">
                            <b>Category &nbsp; : &nbsp; </b> {project?.category.join(' , ')} <br></br>
                            <b>Date &nbsp; : &nbsp; </b> {project?.date} <br></br>
                            <b>Technologies &nbsp; : &nbsp; </b> {project?.technologies.join(' , ')} <br></br>
                            <b>Information &nbsp; : &nbsp; </b> {project?.info} <br></br>
                        </p>
                    </div>
                </div>

                <div className="col-lg-12">

                </div>

            </div>

            <CloseBtn togglePopup={() => {
                setShowPopup(false);
                props.closePopup();

                setTimeout(() => {
                    ctx.setActivePortfolio(null);
                }, 1)


            }} black={false} />
        </div>
    )
}

export default SinglePortfolio;