import './assets/css/bootstrap.min.css';
import React, { Suspense, useMemo, useState, useEffect, useRef, useContext } from 'react';

import { Canvas, useThree, extend, useFrame } from '@react-three/fiber';
import * as THREE from "three";
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import GLTFModal from './components/GLTFModal';
import SidePopup from './components/SidePopup';
import './assets/css/main.css';
import Menu from './components/Menu';

// side Pages
import Connect from './SideAreas/Connect';
import About from './SideAreas/About';
import Portfolio from './SideAreas/Portfolio';
import Services from './SideAreas/Services';
import SinglePortfolio from './SideAreas/SinglePortfolio';
import MainContext from './store/main-context';

extend({ OrbitControls });



const Scene = (props) => {
    const { camera, gl: { domElement } } = useThree();
    const [isLoading, setIsLoading] = useState(props.isLoading);
    const [activeSidebar, setActiveSidebar] = useState(props.activeSidebar);


    useEffect(() => {
        setActiveSidebar(props.activeSidebar);
        setIsLoading(props.isLoading)
    }, [props.activeSidebar, props.isLoading]);

    const [refreshX, setRefreshX] = useState(false);
    const [refreshY, setRefreshY] = useState(false);
    const [refreshZ, setRefreshZ] = useState(false);

    useEffect(() => {
        camera.position.z = 1500;
        camera.position.x = 0;
        camera.position.y = 0;
        zoomIn();
    }, []);

    const controls = useRef();
    useFrame((state) => {
        controls.current.update();
    });

    const initialCameraReset = () => {
        if (window.screen.width < 500) {
            return null;
        }
        let intervalX = setInterval(() => {
            if (camera.position.x < 1 && camera.position.x >= 0) {
                clearInterval(intervalX);
            }
            if (camera.position.x > 1) camera.position.x -= 1;
            else if (camera.position.x <= 0) camera.position.x += 1;
        }, 0.1);
        let intervalY = setInterval(() => {
            if (camera.position.y < 1 && camera.position.y >= 0) {
                clearInterval(intervalY);
            }
            if (camera.position.y > 1) camera.position.y -= 1;
            else if (camera.position.y <= 1) camera.position.y += 1;
        }, 0.1);
        let intervalZ = setInterval(() => {
            if (camera.position.z < 100 && camera.position.z >= 99 && window.innerWidth > 500) {
                clearInterval(intervalZ);
            }
            // else if (camera.position.z < 20 && camera.position.z >= 19.5 && window.innerWidth <= 500) {
            //     clearInterval(intervalZ);
            // }
            if (camera.position.z > 100) camera.position.z -= 1;
            else if (camera.position.z <= 100) camera.position.z += 1;
        }, 0.1);
    }

    const zoomIn = () => {
        let interval = setInterval(() => {

            if (camera.position.z >= 1000) camera.position.z -= 0.25;
            else if (camera.position.z >= 800) camera.position.z -= 0.5;
            else if (camera.position.z >= 600) camera.position.z -= 0.75;
            else if (camera.position.z >= 300) camera.position.z -= 0.9;
            else
                camera.position.z -= 1;

            if (camera.position.z < 100 && window.innerWidth > 500) {
                clearInterval(interval);
            } else if (camera.position.z < 20 && window.innerWidth <= 500) {
                clearInterval(interval);
            }
            console.log(camera.position.z)
        }, 0.1);
    }

    useEffect(() => {
        //weeeeee animation 
        setRefreshX(false);
        setRefreshY(false);
        setRefreshZ(false);
        if (props.falconLoaded && props.bb8Loaded && props.yodaLoaded && props.mandoLoaded && !isLoading) {

        }
        if (!isLoading) {
            if (camera.position.z >= 3000) {


            } else if (!refreshX && !refreshY && !refreshZ && props.p1 && props.p2 && props.p3 && props.p4) {
                // let intervalX = setInterval(() => {
                //     if (camera.position.x <= 0.5 && camera.position.x >= 0) {
                //         clearInterval(intervalX);
                //         setRefreshX(true);
                //         // console.log("clear x")
                //     }
                //     if (camera.position.x > 1) camera.position.x -= 0.5;
                //     else if (camera.position.x <= 0) camera.position.x += 0.5;
                // }, 0.1);
                // let intervalY = setInterval(() => {
                //     if (camera.position.y <= 0.5 && camera.position.y >= 0) {
                //         // console.log("clear y")
                //         clearInterval(intervalY);
                //         setRefreshY(true);
                //     }
                //     if (camera.position.y > 0.5) camera.position.y -= 0.5;
                //     else if (camera.position.y <= 0.5) camera.position.y += 0.5;
                // }, 0.1);
                // let intervalZ = setInterval(() => {
                //     if (camera.position.z < 252 && camera.position.z >= 250) {
                //         clearInterval(intervalZ);
                //         // console.log("clear z")
                //         setRefreshZ(true);
                //     }
                //     if (camera.position.z > 250) camera.position.z -= 1;
                //     else if (camera.position.z <= 250) camera.position.z += 1;
                // }, 0.1);
            }
        }
    }, [props.refreshAnimation, props.falconLoaded, props.bb8Loaded, props.yodaLoaded, props.mandoLoaded, isLoading]);

    useEffect(() => {
        if (props.initialCameraReset !== null && props.falconLoaded && props.bb8Loaded && props.yodaLoaded && props.mandoLoaded && !isLoading) {
            initialCameraReset();
        }
    }, [props.initialCameraReset, props.falconLoaded, props.bb8Loaded, props.yodaLoaded, props.mandoLoaded, isLoading])

    // useEffect(() => {
    //     if (refreshX && refreshY && refreshZ) {
    //         setRefreshX(false);
    //         setRefreshY(false);
    //         setRefreshZ(false);
    //         setTimeout(() => {
    //             zoomIn();
    //         }, 500);
    //     }
    // }, [refreshX, refreshY, refreshZ]);

    let ratio = (window.innerWidth / window.innerHeight);
    let Yoda, Falcon, Bb8, Mando;

    if (window.innerWidth < 400) {
        Yoda = {
            scale: 0.5 * 50 * ratio,
            position: {
                x: 0,
                y: -500 * ratio,
                z: 0
            }
        }

        Mando = {
            scale: 0.5 * ratio * 2,
            position: {
                x: 10 * ratio,
                y: 600 * ratio,
                z: 100 * ratio
            },
        }



        Falcon = {
            scale: 0.5 * 0.8 * ratio,
            position: {
                x: 0,
                y: 0,
                z: 0,
            },
        }

        Bb8 = {
            scale: 0.5 * 2.25 * ratio,
            position: {
                x: 500 * ratio,
                y: -1300 * ratio,
                z: 0
            }
        }

    } else {
        Yoda = {
            scale: 3,
            position: {
                x: 10,
                y: 10,
                z: 0
            }
        }

        Mando = {
            scale: 0.1,
            position: {
                // x: 0.5 * -150,
                // y: 0.5 * 100,
                // // z: 150
                // z: 0
                x: -25,
                y: 25,
                // z: 150
                z: 0
            }
        }

        Falcon = {
            scale: 0.05,
            position: {
                // x: 0.5 * 600,
                // y: 0,
                // z: 0
                x: 50,
                y: 0,
                z: 0
            }
        }

        Bb8 = {
            scale: 0.15,
            position: {
                // x: 0.5 * -500,
                // y: 0.5 * -100,
                // z: 0
                x: -50,
                y: -10,
                z: 0
            }
        }

    }


    return (
        <>
            <ambientLight intensity={0.1} />
            <spotLight intensity={1} position={[300, 300, 1000]} />
            <orbitControls ref={controls} args={[camera, domElement]} />

            {/* Mando  */}
            <Suspense fallback={<Fallback setLoaded={() => { props.setMandoLoaded(true); }} />}>
                <GLTFModal path="/gltf/mando_helmet/scene.gltf"
                    rotationSpeed={[0, 0.009, 0]}
                    rotation={[0, 0, 0]}
                    animatePosition={[35 * ratio, -10 * ratio, 3]}
                    animatePlanet={(activeSidebar == "about" ? true : false)}
                    position={[Mando.position.x, Mando.position.y, Mando.position.z]}

                    planetType="about"
                    scale={[Mando.scale, Mando.scale, Mando.scale]}
                    goLeft={(activeSidebar == "about" || activeSidebar == "" || activeSidebar == null ? false : true)}
                    onPointerDown={() => {
                        initialCameraReset();
                        props.setActiveSidebar("about")
                    }} />
            </Suspense>

            {/* Falcon  */}
            <Suspense fallback={<Fallback setLoaded={() => props.setFalconLoaded(true)} />}>
                <GLTFModal path="/gltf/falcon1/scene.gltf"
                    rotationSpeed={[0, 0.009, 0]}
                    rotation={[0, 0, 0]}
                    // animatePosition={[2.3 * (window.innerWidth / window.innerHeight), 0, 3]}
                    animatePosition={[33 * ratio, -3 * ratio, 3]}
                    animatePlanet={(activeSidebar == "portfolio" ? true : false)}
                    position={[Falcon.position.x, Falcon.position.y, Falcon.position.z]}
                    planetType="portfolio"
                    scale={[Falcon.scale, Falcon.scale, Falcon.scale]}
                    goLeft={(activeSidebar == "portfolio" || activeSidebar == "" || activeSidebar == null ? false : true)}
                    onPointerDown={() => {
                        initialCameraReset();
                        props.setActiveSidebar("portfolio")

                    }} />
            </Suspense>

            {/* bb8  */}
            <Suspense fallback={<Fallback setLoaded={() => props.setBb8Loaded(true)} />}>
                <GLTFModal path="/gltf/bb/scene.gltf"
                    rotationSpeed={[0, 0.009, 0]}
                    rotation={[0, 0, 0]}
                    animatePosition={[33 * ratio, 0, 3]}
                    animatePlanet={(activeSidebar == "connect" ? true : false)}
                    position={[Bb8.position.x, Bb8.position.y, Bb8.position.z]}
                    planetType="connect"
                    scale={[Bb8.scale, Bb8.scale, Bb8.scale]}
                    goLeft={(activeSidebar == "connect" || activeSidebar == "" || activeSidebar == null ? false : true)}
                    onPointerDown={() => {
                        initialCameraReset();
                        props.setActiveSidebar("connect")
                    }} />
            </Suspense>

            {/* yoda  */}
            <Suspense fallback={<Fallback setLoaded={() => props.setYodaLoaded(true)} />}>
                <GLTFModal path="/gltf/yoda/scene.gltf"
                    rotationSpeed={[0, 0.009, 0]}
                    rotation={[0, 0, 0]}
                    animatePosition={[(33 * ratio), 0, 3]}
                    animatePlanet={(activeSidebar == "services" ? true : false)}
                    position={[Yoda.position.x, Yoda.position.y, Yoda.position.z]}
                    planetType="services"
                    scale={[Yoda.scale, Yoda.scale, Yoda.scale]}
                    goLeft={(activeSidebar == "services" || activeSidebar == "" || activeSidebar == null ? false : true)}
                    onPointerDown={() => {
                        props.setActiveSidebar("services")
                        initialCameraReset();
                    }} />
            </Suspense>
        </>
    )
}


const Fallback = (props) => {
    useEffect(() => {
        return () => {
            props.setLoaded();
        }
    }, [])
    return null;
}

function App() {

    const [stars, setStars] = useState([]);
    const [initialCameraReset, setInitialCameraReset] = useState(false);
    const [windowLoaded, setWindowLoaded] = useState(false);

    const [falconLoaded, setFalconLoaded] = useState(false);
    const [yodaLoaded, setYodaLoaded] = useState(false);
    const [mandoLoaded, setMandoLoaded] = useState(false);
    const [bb8Loaded, setBb8Loaded] = useState(false);


    const [fadeOutIntroText, setFadeOutIntroText] = useState(false);
    const [fadeOutCrawlText, setFadeOutCrawlText] = useState(true);

    const [isLoading, setIsLoading] = useState(true);

    const [introAnimationLoaded, setIntroAnimationLoaded] = useState(false);

    const [menuOpen, setMenuOpen] = useState(false);
    const [activeSidebar, setActiveSidebar] = useState(false);
    const [fadeInItems, setFadeInItems] = useState(false);

    const ctx = useContext(MainContext);
    const [activePortfolioPopup, setActivePortfolioPopup] = useState(false);


    useEffect(() => {
        setTimeout(() => {
            setFadeOutIntroText(true);
            setTimeout(() => {
                setFadeOutCrawlText(false);
            }, 500);
        }, 2000);
        setTimeout(() => {
            setIntroAnimationLoaded(true);

        }, 10000);

    }, []);

    useEffect(() => {
        if (falconLoaded && yodaLoaded && mandoLoaded && bb8Loaded && isLoading && introAnimationLoaded) {
            setFadeOutIntroText(true);
            setIsLoading(false);
            setWindowLoaded(true);

            setTimeout(() => {
                setFadeInItems(true);
            }, 500);
        } else if (introAnimationLoaded && fadeOutIntroText && fadeOutCrawlText) {
            setFadeOutIntroText(false);
        }
    }, [falconLoaded, fadeOutIntroText, isLoading, yodaLoaded, mandoLoaded, bb8Loaded, fadeOutIntroText, introAnimationLoaded]);

    useEffect(() => {
        let newStars = [];
        for (let i = 0; i < 400; i++) {
            newStars.push({
                style: {
                    position: 'absolute',
                    top: Math.random() * 100 + '%',
                    left: Math.random() * 100 + '%',
                    transform: `translateY(-50%) scale(${Math.random() * 1.5 + 0.2})`
                }
            });
        }
        setStars(newStars);
    }, []);




    useEffect(() => {
        setActivePortfolioPopup(ctx.activePorfolio);

    }, [ctx]);
    const pages = () => {

        // if (!isLoading) {

        return (
            <>

                <SidePopup
                    closeSidebar={closeSidebar}
                    active={(activeSidebar === "connect" ? true : false)}>
                    <Connect />
                </SidePopup>

                <SidePopup
                    closeSidebar={closeSidebar}
                    active={(activeSidebar === "about" ? true : false)}>
                    <About />
                </SidePopup>

                <SidePopup
                    closeSidebar={closeSidebar}
                    active={(activeSidebar === "services" ? true : false)}>
                    <Services />
                </SidePopup>

                <SidePopup
                    closeSidebar={closeSidebar}
                    active={(activeSidebar === "portfolio" ? true : false)}>
                    <Portfolio />
                </SidePopup>


            </>
        )
        // }
    }
    console.log(activePortfolioPopup);

    const closeSidebar = () => {
        setActiveSidebar('');
    }


    return (
        <>
            <div className={"bg-linear linear-default "} />
            <div className={"bg-linear linear-connect " + (activeSidebar === "connect" ? ' show ' : ' ')} />
            <div className={"bg-linear linear-about " + (activeSidebar === "about" ? ' show ' : ' ')} />
            <div className={"bg-linear linear-services " + (activeSidebar === "services" ? ' show ' : ' ')} />
            <div className={"bg-linear linear-portfolio " + (activeSidebar === "portfolio" ? ' show ' : ' ')} />

            <div className={"  fade_transition" + (!fadeInItems ? ' hide ' : '')}>
                <Menu
                    {...{ setActiveSidebar, setMenuOpen, menuOpen, activeSidebar }}
                />
            </div>
            <div className={"star_wars_wrapper intro_text_wrapper d-flex align-items-center justify-content-center  "}>
                <div className="container-fluid">
                    <div className="row d-flex justify-content-center">
                        <div className="col-lg-8 position-relative d-flex justify-content-center">
                            <span className={"  mx-auto intro__text  fade_transition " + ((fadeOutIntroText) ? '  hide' : ' ')}    >
                                <span>A long time ago in a galaxy far, <br></br>far away....</span>
                            </span>

                            <div className={"    fade_transition " + ((fadeOutCrawlText) ? ' hide  ' : '  ')}    >
                                <section class="star-wars">
                                    <div class="crawl">
                                        <div class="title">
                                            <p>Episode XXI</p>
                                            <h1>A New Beginning</h1>
                                        </div>

                                        <p>The galaxy has heard your mysterious broadcast for searching a software developer ! </p>
                                        <p>Hi my name is Serouj, and I am your Solution.</p>
                                        <p>  You are only minuites away from turning your idea to reality.</p>
                                    </div>
                                </section>
                            </div>
                        </div>
                    </div>
                </div>

            </div>

            <div class="stars" id="starscontainer">
                {
                    stars.map(star => (
                        <div className="single-star" {...star}></div>
                    ))
                }
            </div>

            <div className={" canvas_wrrapper fade_transition" + (!fadeInItems ? ' hide ' : '')}>
                <Canvas>
                    <Scene
                        {...{
                            setFalconLoaded, setBb8Loaded, setYodaLoaded, setMandoLoaded, bb8Loaded, mandoLoaded, yodaLoaded, falconLoaded,
                            isLoading, initialCameraReset, activeSidebar, setActiveSidebar

                        }} />
                </Canvas>
            </div>

            {pages()}

            <SinglePortfolio
                closePopup={() => { setActivePortfolioPopup(null) }}
                showPopup={(!activePortfolioPopup) ? false : true} />

        </ >
    )
}
export default App;
